"use client";

import { useState } from "react";
// import Filter from "./Filter"; // your Filter component path
// import BestSellers from "./BestSellers"; // your BestSellers component path
import MainContent from "./MainContent"; // your MainContent component path if needed
import BestSellers from "../bestSellers/BestSellers";
import Filter from "../filter/Filter";

const currencyRates = {
  USD: 1,
  GEL: 3.1,  // Example: 1 USD = 3.1 GEL
};

const Page = () => {
  const [language, setLanguage] = useState<"en" | "ka">("en");
  const [currency, setCurrency] = useState<"USD" | "GEL">("USD");

  return (
    <>
      <Filter
        language={language}
        setLanguage={setLanguage}
        currency={currency}
        setCurrency={setCurrency}
      />
      <MainContent currency={currency} />
      <BestSellers currency={currency} currencyRates={currencyRates} />
    </>
  );
};

export default Page;
